import {coordsType, objectType} from "../misc/types";

export const getBounds = (objs: objectType[]) => {
    // -
    //      coordsType[]
    //   2gis api
    //      
    //   zoom 
    let bounds: coordsType[] = []
    for (let i = 0; i < objs.length; i++) {
        const obj = objs[i]
        bounds.push(obj.coords)
    }
    return bounds
}